<template>
  <div>
    <!---->
    <Breadcrumb class="breadcrumb">
      <BreadcrumbItem>
        <Icon type="ios-home-outline"></Icon>信息通知
      </BreadcrumbItem>
      <BreadcrumbItem class="curBreadcrumb">
        <Icon type="logo-buffer"></Icon> 信息通知
      </BreadcrumbItem>
      <span class="screenContent">
        <img
          style="width: 11px;margin-right: 8px;margin-left: 10px;"
          src="../assets/day.png"
        >
        {{ curTime }}</span>
    </Breadcrumb>
    <Col
      class="modelTable"
      span="21"
    >
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Divider orientation="left">
        2019.03.10
      </Divider>
      <Table
        id="model1"
        ref="checkTable"
        class="table"
        :columns="columns"
        :data="data"
      >
      </Table>
    </Content>
    </Col>
    <!--锚点-->
    <Col
      span="3"
      style="padding-left: 4.8%;"
    >
    <Anchor show-ink>
      <AnchorLink
        href="#model1"
        title="2019.03.10"
      />
      <AnchorLink
        href="#model2"
        title="2019.03.22"
      />
    </Anchor>
    </Col>
    <!--/锚点-->
    <!---->
    <!--详情弹出框-->
    <Modal
      v-model="selTypeMod"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span>请添加表单信息</span>
      </p>
      <Tabs
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="formValidateType"
            :model="formValidateType"
            :rules="ruleValidateType"
            :label-width="80"
          >
            <Row>
              <Col span="11">
              <FormItem
                label="表单类型"
                prop="name"
              >
                <Select
                  v-model="formValidateType.quality"
                  :transfer="transferValue"
                  filterable
                  placeholder="请选择表单类型"
                >
                  <Option value="0">
                    申报类模板
                  </Option>
                  <Option value="1">
                    检查类模板
                  </Option>
                  <Option value="2">
                    验收类模板
                  </Option>
                </Select>
              </FormItem>
              </Col>
              <!--<Col span="11"></Col>-->
              <Col
                span="11"
                offset="2"
              >
              <FormItem
                label="资金类别"
                prop="quality"
              >
                <Select
                  v-model="formValidateType.quality"
                  :transfer="transferValue"
                  filterable
                  placeholder="请选择机构性质"
                >
                  <Option value="0">
                    政府机关
                  </Option>
                  <Option value="1">
                    事业法人
                  </Option>
                  <Option value="2">
                    合作组织
                  </Option>
                  <Option value="3">
                    企业法人
                  </Option>
                </Select>
              </FormItem>
              </Col>
            </Row>
            <Col span="24">
            <FormItem
              label="表单名称"
              prop="quality"
            >
              <Input
                v-model="formValidateType.remark"
                placeholder="请填写表单名称"
              ></Input>
            </FormItem>
            </Col>
            <Col span="24">
            <FormItem label="使用说明">
              <Input
                v-model="formValidateType.remark"
                type="textarea"
                :autosize="{minRows: 1,maxRows: 5}"
                placeholder="请填写使用说明"
              ></Input>
            </FormItem>
            </Col>
          </Form>
        </TabPane>
        <TabPane
          label="PDF附件"
          name="name7"
        >
          <Icon
            size="16"
            type="md-alert"
          />
          <b>请上传与设置信息一致的附件或证明性文件，本系统只接受PDF格式文件，请上传前进行确认。</b>
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <span>点击此处上传</span>
          </Upload>
        </TabPane>
        <TabPane
          label="WORD附件"
          name="name8"
        >
          <Icon
            size="16"
            type="md-alert"
          />
          <b>请上传与设置信息一致的附件或证明性文件，本系统只接受WORD格式文件，请上传前进行确认。</b>
          <Upload action="//jsonplaceholder.typicode.com/posts/">
            <span>点击此处上传</span>
          </Upload>
        </TabPane>
      </Tabs>
      <p slot="footer">
        <Button
          style="float: left"
          icon="md-print"
        >
          打印
        </Button>
        <Button
          style="float: left"
          icon="md-exit"
        >
          导出
        </Button>
        <Button
          type="primary"
          @click="submitType('formValidateType')"
        >
          确认
        </Button>
        <Button
          style="margin-left: 8px"
          @click="resetType('formValidateType')"
        >
          取消
        </Button>
      </p>
    </Modal>
    <!--/详情弹出框-->
  </div>
</template>

<script>
export default {
  name: 'Massage',
  data() {
    return {
      transferValue: true,
      //
      formValidateType: {
        name: '',
        quality: '',
        remark: ''
      },
      ruleValidateType: {
        name: [
          { required: true, message: '请填写机构名称', trigger: 'blur' }
        ],
        quality: [
          { required: true, message: '请选择机构性质', trigger: 'change' }
        ]
      },
      //
      // 筛选内容
      screen: {
        yW: '',
        zG: '',
        zJ: ''
      },
      curTime: '',
      showContact: false,
      selTypeMod: false,
      cityList: [
        {
          value: 'New York',
          label: 'New York'
        },
        {
          value: 'London',
          label: 'London'
        },
        {
          value: 'Sydney',
          label: 'Sydney'
        },
        {
          value: 'Ottawa',
          label: 'Ottawa'
        },
        {
          value: 'Paris',
          label: 'Paris'
        },
        {
          value: 'Canberra',
          label: 'Canberra'
        }
      ],
      checkText: '提交审核',
      columns: [
        {
          title: 'Action',
          className: 'imgStyle',
          key: 'action',
          width: 20,
          render: (h, params) => {
            return h('div', [
              h('Icon', {
                props: {
                  type: 'ios-document',
                  color: '#2D8CF0'
                },
                style: {
                  fontSize: '14px'
                },
                on: {
                  click: () => {
                    console.log(111)
                  }
                }
              })
            ]);
          }
        },
        {
          className: 'noPaddingLeft blueCol',
          title: '',
          key: 'name',
          width: 550,
          align: 'left',
          render: (h, params) => {
            return h('div', [
              h('Button', {
                props: {
                  type: 'text',
                  size: 'small',
                  color: '#2D8CF0'
                },
                on: {
                  click: () => {
                    this.showInfo()
                  }
                }
              }, '关于天津市农药包装废弃物回收处理工作验收通知')
            ]);
          }
        },
        {
          title: '发布机构',
          key: 'add'
        },
        {
          className: 'noPaddingRight',
          title: 'Action',
          key: 'action',
          width: 60,
          render: (h, params) => {
            return h('div', [
              h('Icon', {
                props: {
                  type: 'ios-download-outline'
                },
                style: {
                  fontSize: '18px',
                  color: '#559DF9'
                },
                on: {
                  click: () => {
                    console.log(111)
                  }
                }
              })
            ]);
          }
        }
      ],
      data: [
        {
          add: '天津市农业农村委员会',
          age: '竣工验收证明书',
          img: '0'
        },
        {
          name: 'Jim Green',
          age: '工程质量保修书',
          img: '0'
        },
        {
          name: 'Joe Black',
          age: '竣工验收证明书',
          img: '0'
        },
        {
          name: 'Jon Snow',
          age: '工程质量保修书',
          img: '0'
        }
      ]
    }
  },
  mounted() {
    var myDate = new Date();
    const time = myDate.toLocaleString('chinese', { hour12: false });
    this.curTime = time.substring(0, 15)
  },
  methods: {
    submitType(name) {
      this.$refs[name].validate((valid) => {
        if (valid) {
          // this.$Message.success('Success!');
        }
      })
    },
    resetType(name) {
      this.$refs[name].resetFields();
      this.selTypeMod = false;
    },
    addInnerData() {
      this.innerData.push({
        name: '新加的',
        age: 18,
        birthday: '919526400000',
        address: '北京市朝阳区芍药居'

      })
    },
    // 弹框表格
    handleEdit(row, index) {
      this.editName = row.name;
      this.editAge = row.age;
      this.editAddress = row.address;
      this.editfenxiang = row.address;
      this.editBirthday = row.birthday;
      this.editIndex = index;
    },
    handleSave(index) {
      this.data[index].name = this.editName;
      this.data[index].age = this.editAge;
      this.data[index].birthday = this.editBirthday;
      this.data[index].address = this.editAddress;
      this.data[index].fenXiang = this.editfenxiang;
      this.editIndex = -1;
    },
    getBirthday(birthday) {
      const date = new Date(parseInt(birthday));
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      return `${year}-${month}-${day}`;
    },
    // 弹框表格结束
    ok() {

    },
    cancel() { },
    // 打开详情弹出来
    showInfo() {
      this.selTypeMod = true
    },
    showCheck() {
      this.checkText = '正在审核'
      this.$Modal.confirm({
        title: '是否确认提交审核',
        content: '',
        onOk: () => {
          this.$Message.info('Clicked ok');
        },
        onCancel: () => {
          this.$Message.info('Clicked cancel');
        }
      });
    }
  }
}
</script>

<style>
div.ivu-card-body {
  padding: 0;
}
.modelTable div.ivu-table-wrapper {
  border: none;
}
.modelTable .ivu-table td,
.ivu-table th {
  border-bottom: 0;
  line-height: 48px !important;
}
.modelTable .ivu-table-header {
  display: none;
}
.ivu-drawer-body > div {
  margin: 20px 0;
}
.ivu-drawer-body > div b {
  margin-left: 3px;
}
/*<!--选择弹出框的样式-->*/
.selStyle .ivu-radio-wrapper {
  font-weight: bold;
}
.selStyle .ivu-modal-body {
  line-height: 2rem;
}
.selStyle .ivu-radio-wrapper {
  margin-right: 21px;
}
.ivu-page-options-sizer {
  margin-right: 0;
}
.table {
  margin: 15px 0;
}
.breadcrumb i {
  margin-right: 8px;
}
.ivu-breadcrumb {
  font-size: 12px;
  margin: 20px 0;
}
.ivu-menu-submenu-title {
  font-weight: bold;
}
.ivu-menu-submenu-title span > i,
.ivu-menu-submenu-title > i {
  margin-right: 4px;
}
.ivu-menu-vertical .ivu-menu-item,
.ivu-menu-vertical .ivu-menu-submenu-title {
  font-size: 12px;
  color: #495060;
}
.ivu-menu-light.ivu-menu-vertical .ivu-menu-item-active:not(.ivu-menu-submenu) {
  font-weight: bold;
}
.ivu-menu-submenu.ivu-menu-opened {
  padding-top: 8px;
}
.modelTable .content {
  height: 100%;
}
.imgStyle .ivu-table-cell {
  padding-left: 0;
  width: 20px;
  padding-right: 0;
  text-align: right;
}
.noPaddingLeft .ivu-table-cell {
  padding-left: 0;
}
.noPaddingRight .ivu-table-cell {
  text-align: right;
  padding-right: 0;
}
.ivu-table:before {
  height: 0;
}
.ivu-table:after {
  height: 0;
}
</style>



